.call-action-wrapper {
  width: 100%;
  display: flex;
  justify-content: center;
  position: relative;
}
.call-action-box {
  width: 50%;
  position: absolute;
  transform: translateY(-50%);
  background-color: hsl(217, 20%, 14%);
  padding: 2rem 5rem;
  text-align: center;
  border-radius: 1rem;
  box-shadow: 5px 5px 2px hsla(214, 26%, 5%, 0.8);
}

.call-action-box > h2 {
  margin-bottom: 1rem;
}

.call-action-box > p {
  margin-bottom: 2rem;
}

.email-form {
  width: 100%;
  display: flex;
  justify-content: center;
  gap: 1.5rem;
}

.email {
  width: 60%;
  padding: 1rem 2rem;
  color: black;
}

.email::placeholder {
  color: rgba(128, 128, 128, 0.6);
}

.email,
.email-btn {
  font-family: "Open Sans", sans-serif;
  border-radius: 3rem;
  border: none;
}

.email-btn {
  width: 25%;
  cursor: pointer;
  background: linear-gradient(110deg, hsl(176, 68%, 64%), hsl(198, 60%, 50%));
}

/********** media query **********/

@media (max-width: 769px) {
  .call-action-wrapper {
    width: 90vw;
    margin: auto;
  }

  .call-action-box {
    width: 100%;
    padding: 3rem 2rem;
  }
}

@media (max-width: 688px) {
  .email-form {
    flex-direction: column;
  }

  .email {
    width: 100%;
  }

  .email-btn {
    width: 100%;
    padding: 1rem;
    font-size: 1rem;
  }
}
