#main-content {
  background-color: hsl(218, 28%, 13%);
  margin: -1px 0 0;
}

.content-container {
  display: grid;
  grid-template-columns: 1fr 1fr;
  margin: auto;
}

/********** four contents section **********/

.content-container {
  gap: 5rem;
  text-align: center;
  width: 65%;
  padding: 12rem 0 10rem;
}

.content {
  margin: auto;
  max-width: 68%;
}

.content > h2 {
  margin: 1rem 0 0.5rem;
  font-size: 1.8rem;
}

.content > small {
  font-size: 1rem;
  line-height: 1.5;
}

/********** stay productive section **********/

.stay-productive-container {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 4rem;
  align-items: center;
}

.info-text-section {
  display: flex;
  flex-direction: column;
  gap: 2rem;
}

.productive-img-container {
  margin: auto 0 auto auto;
}

.productive {
  width: 37vw;
}

.info-text {
  display: flex;
  flex-direction: column;
  justify-content: center;
  gap: 2rem;
  width: 30vw;
}

.info-text > small {
  font-size: 1rem;
  line-height: 1.5;
}

.info-text > h1 {
  font-size: 3rem;
}

.info-link {
  color: cyan;
  width: fit-content;
  display: flex;
  gap: 0.5rem;
  align-items: center;
  border-bottom: 1px solid cyan;
}

.info-link:hover {
  color: white;
  border-bottom: 1px solid white;
}

/********** media query **********/

@media (max-width: 769px) {
  .content-container,
  .stay-productive-container {
    grid-template-columns: 1fr;
  }

  .productive {
    width: 80vw;
  }

  .info-text {
    width: 85vw;
    margin: auto;
  }

  .info-text > h1 {
    font-size: 5vw;
    text-align: center;
  }

  .productive-img-container {
    margin: auto;
  }

  .info-link {
    margin: auto;
  }
}

@media (max-width: 543px) {
  .content-container {
    width: 100%;
    gap: 7rem;
  }

  .content {
    max-width: 70%;
  }
}

@media (max-width: 426px) {
  .info-text > h1 {
    font-size: 7vw;
    text-align: left;
  }
}
