footer {
  background-color: hsl(216, 53%, 9%);
  padding: 12rem 6rem 5rem;
}

.footer-wrapper {
  display: flex;
  flex-direction: column;
  gap: 2rem;
}

.footer-container {
  display: grid;
  grid-template-columns: 3fr 2fr repeat(3, 1fr);
}

.footer-desc-container {
  display: grid;
  grid-template-columns: 1fr 9fr;
  width: 80%;
}

.location-logo {
  width: 1rem;
}

.footer-contact-container {
  display: flex;
  flex-direction: column;
  gap: 2rem;
}

.footer-phone-email {
  display: flex;
  gap: 1.5rem;
  align-items: center;
}

.aboutus-container,
.contactus-container {
  display: flex;
  flex-direction: column;
  gap: 1rem;
}

.socials {
  display: flex;
  gap: 1rem;
}

/********** media query **********/

@media (max-width: 769px) {
  .footer-container {
    grid-template-columns: 1fr;
  }

  .footer-desc-container {
    margin-bottom: 2rem;
  }

  .footer-contact-container {
    gap: 1rem;
    margin-bottom: 5rem;
  }

  .aboutus-container,
  .contactus-container {
    margin-bottom: 3.5rem;
  }

  .socials {
    margin: auto;
  }

  footer {
    padding: 17rem 6rem 5rem;
  }
}

@media (max-width: 426px) {
  .footer-desc-container {
    padding: 0;
  }

  footer {
    padding: 19rem 3rem 4rem;
  }
}
