.header-wrapper {
  background: hsl(217, 28%, 15%);
  width: 100%;
  min-height: 100vh;
  background-image: url("../images/bg-curvy-desktop.svg");
  background-repeat: no-repeat;
  background-position: bottom;
  background-size: 100% auto;
}

.header-section {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  gap: 2rem;
  text-align: center;
}

.header-img {
  margin-bottom: 2rem;
  width: 40%;
}

.header-heading {
  width: 55vw;
}

.header-description {
  width: 45vw;
}

.header-heading {
  font-size: 3rem;
}

.header-description {
  font-size: 1.1rem;
  line-height: 1.6;
  font-weight: lighter;
}

.sign-up-btn {
  border: none;
  padding: 1rem;
  width: 15rem;
  border-radius: 2rem;
  background: linear-gradient(110deg, hsl(176, 68%, 64%), hsl(198, 60%, 50%));
  font-family: "Open Sans", sans-serif;
  font-size: 1rem;
  cursor: pointer;
  font-weight: 700;
}

.sign-up-btn:hover {
  background: hsl(176, 51%, 69%);
}

/************ Media Query ************/

@media (max-width: 1441px) {
  .header-img {
    width: 55vw;
  }

  .header-section {
    padding: 0 20vw;
  }

  .header-heading {
    font-size: 3vw;
  }
  .header-description {
    font-size: 1.5vw;
  }
}

@media (max-width: 769px) {
  .header-section {
    padding: 0 13vw;
  }
  .header-heading {
    font-size: 5vw;
    width: 80vw;
  }
  .header-description {
    font-size: 2.5vw;
    width: 75vw;
  }

  .header-wrapper {
    min-height: 0vh;
    background-position: bottom;
    background-size: 150% auto;
  }
}

@media (max-width: 426px) {
  .header-img {
    width: 100vw;
    padding: 1.5rem;
  }

  .header-section {
    padding: 0 8vw;
  }

  .header-heading {
    font-size: 8vw;
  }
  .header-description {
    font-size: 4vw;
  }
  .sign-up-btn {
    font-size: 4vw;
    width: 60vw;
  }
}
