.navbar-wrapper {
  background-color: hsl(217, 28%, 15%);
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
  font-family: "Raleway", sans-serif;
  padding: 2rem 3rem;
}

.navbar-list {
  display: flex;
  gap: 3rem;
}

.logo-img {
  min-width: 10vw;
}

.navbar-link,
.navbar-link > a {
  list-style: none;
  text-decoration: none;
}

.navbar-link {
  width: 70px;
  text-align: center;
}

.navbar-link > a:hover {
  font-weight: 700;
  border-bottom: 1px solid white;
}

/************ Media Query ************/

@media (max-width: 769px) {
  .navbar-wrapper {
    padding: 4vw 5vw 4rem;
  }

  .logo-img {
    width: 20vw;
  }

  .navbar-list {
    gap: 2rem;
    font-size: 2.5vw;
  }

  .navbar-link {
    width: unset;
  }
}

@media (max-width: 426px) {
  .navbar-wrapper {
    padding: 9vw 8vw;
  }

  .logo-img {
    width: 30vw;
  }

  .navbar-list {
    gap: 1.5rem;
    font-size: 3.5vw;
  }
}
