.testimonials-wrapper {
  width: 100%;
  display: flex;
  gap: 3.5rem;
  justify-content: center;
  margin: 12rem 0 19rem;
}

.quote-icon {
  position: absolute;
  top: -2.2rem;
  left: -0.5rem;
  z-index: -1;
}

.testimonial {
  background-color: hsl(217, 28%, 15%);
  width: 500px;
  padding: 2.5rem;
  box-shadow: 10px 10px hsl(218, 28%, 12%);
  position: relative;
}

.profile {
  display: flex;
  align-items: center;
  gap: 1rem;
  margin-top: 1rem;
}

.profile-pic {
  border-radius: 50%;
  width: 3rem;
}

/********** media query **********/

@media (max-width: 769px) {
  .testimonials-wrapper {
    flex-direction: column;
  }

  .testimonial {
    width: 75vw;
    margin: auto;
  }
}
